<template>
  <el-tabs v-model="activeName" type="border-card" @tab-click="handleClick">
    <el-dialog title="" :visible.sync="dialogVisible" width="70%">
      <!-- 新建与编辑的弹窗 -->
      <ModifyProject :thisRow="thisRow" :key="timer"></ModifyProject>

      <!-- 上传文件 -->
      <span slot="footer">
        <!-- <el-upload>
                    <el-button prop='action' size="small" type="primary">点击上传</el-button>
                </el-upload> -->
      </span>

      <!-- <span slot="footer" class="dialog-footer">
        <el-button @click="close">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </span> -->
    </el-dialog>

    <!-- 详情 -->
    <!-- <el-dialog title="" :visible.sync="dialogVisible_1" width="70%" v-for="(item,index) in tabledata" > -->
    <el-dialog title="" :visible.sync="dialogVisible_1" width="70%">
      <!-- 详情页的弹窗 -->
      <ViewProject :tabledata="thisRow" :key="timer"></ViewProject>
    </el-dialog>

    <el-dialog title="" :visible.sync="dialogVisible_2" width="80%">
      <!-- 技术资料的弹窗 -->
      <uploadDocument :tabledata="thisRow" :key="timer"></uploadDocument>
    </el-dialog>

    <el-tab-pane label="技术部" name="first" class="manage">
      <div class="manage-header">
        <el-button type="primary" @click="handleAdd"> +新建 </el-button>
        <!-- form搜索区域 -->
        <el-form :inline="true" :model="userform">
          <el-form-item>
            <el-select
              v-model="value"
              multiple
              filterable
              remote
              reserve-keyword
              placeholder="请输入关键词"
              :remote-method="remoteMethod"
              :loading="loading"
            >
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
            <el-button type="primary">查询</el-button>
          </el-form-item>
        </el-form>
      </div>

      <div class="common-tabel">
        <el-table
          stripe
          height="90%"
          :data="item"
          style="width: 100%; height: auto"
          v-for="(item, index) in tabledata"
        >
          <el-table-column prop="project_name" label="项目名称" width="180">
          </el-table-column>
          <el-table-column prop="project_id" label="项目令号" width="180">
          </el-table-column>
          <el-table-column prop="project_create" label="项目创建人" width="180">
          </el-table-column>
          <el-table-column>
            <template slot-scope="scope">
              <el-button
                type="primary"
                size="mini"
                @click="projectInfo(scope.row)"
                >详情</el-button
              >
              <el-button
                type="primary"
                size="mini"
                @click="handleEdit(scope.row)"
                >编辑</el-button
              >
              <el-button
                type="primary"
                size="mini"
                @click="handlePulish(scope.row)"
                >发布</el-button
              >
              <el-button
                type="primary"
                size="mini"
                @click="handleFile(scope.row)"
                >归档</el-button
              >
              <el-button
                type="danger"
                size="mini"
                @click="handleDelete(scope.row)"
                >删除</el-button
              >
              <el-button
                type="info"
                size="mini"
                @click="uploadDocument(scope.row)"
                >技术资料</el-button
              >
            </template>
          </el-table-column>
        </el-table>
      </div>
      <!-- <div class="pager">
                    <el-pagination layout="prev, pager, next" :total="total" @current-change="handlePage">
                    </el-pagination>
                </div> -->
    </el-tab-pane>

    <el-tab-pane label="项目管理部" name="second"> </el-tab-pane>
  </el-tabs>
</template>
<script>
console.log("project:cookie");
console.log(Cookie.get("token"));

import Cookie from "js-cookie";
import axios from "axios";
import { getUser, addUser, editUser, delUser } from "../api";
import ViewProject from "../project/ViewProject.vue";
import ModifyProject from "@/project/modifyProject.vue";
import uploadDocument from "../project/uploadDocument/uploadDocument.vue";
export default {
  components: {
    ViewProject,
    ModifyProject,
    uploadDocument,
  },
  data() {
    return {
      timer: "", //用于控制子组件viewProject，每次点击按钮时重新调用一次子组件
      department: [],
      room: [],
      edit: false,
      thisRow: {},
      activeName: "first",
      dialogVisible: false,
      dialogVisible_1: false,
      dialogVisible_2: false,

      form: {
        //主页面仅展示这三条信息
        project_name: "",
        project_id: "",
        project_create: "",
        project_members: [],
        project_approves: [],
        document_type: [],
        contract_date: "",
        sale_manager: "",
        contract_money: "",
        design_manager: "",
        serve_person: [],
        receive_address: "",
        deliver_way: "",
        order_unit: "",
        receive_unit: "",
        arrive_station: "",
        mail: "",
        contact_people: "",
        contact_people_phone: "",
        fax: "",
        send_unit: "",
        time_dy: "",
        time_sb: "",
        send_condition: "",
      },
      rules: {
        name: [{ required: true, message: "请输入姓名" }],
        project_id: [{ required: true, message: "请输入令号" }],
        department: [{ required: true, message: "请选择科室" }],
      },
      modeltype: 0, //表示新增的弹窗，1表示编辑,
      tabledata: [], //主体的数据列表，作为主要的传递数据
      userform: {
        name: "",
        // project_id: '',
      },
      options: [],
      value: [],
      list: [],
      loading: false,
      states: [
        "Alabama",
        "Alaska",
        "Arizona",
        "Arkansas",
        "California",
        "Colorado",
        "Connecticut",
        "Delaware",
        "Florida",
        "Indiana",
        "Iowa",
        "Kansas",
        "Kentucky",
        "Georgia",
        "Hawaii",
        "Idaho",
        "Illinois",
        "Louisiana",
        "Maine",
        "Maryland",
        "Nebraska",
        "Nevada",
        "New Hampshire",
        "Massachusetts",
        "Michigan",
        "Minnesota",
        "Mississippi",
        "Missouri",
        "Montana",
        "New Jersey",
        "New Mexico",
        "New York",
        "North Carolina",
        "North Dakota",
        "Ohio",
        "Oklahoma",
        "Oregon",
        "Pennsylvania",
        "Washington",
        "West Virginia",
        "Wisconsin",
        "Rhode Island",
        "South Carolina",
        "South Dakota",
        "Tennessee",
        "Texas",
        "Utah",
        "Vermont",
        "Virginia",
        "Wyoming",
      ],
    };
  },
  methods: {
    uploadDocument(row) {
      this.timer = new Date().getTime();
      this.dialogVisible_2 = true;
      this.thisRow = row;
      // console.log(this.thisRow);
    },
    handleClick(tab, event) {
      console.log(tab, event);
    },
    handleAdd() {
      this.timer = new Date().getTime();
      this.modeltype = 1;
      this.dialogVisible = true;
      this.thisRow = {};
      // this.edit = false;
    },
    submit() {
      // console.log(ModifyProject.form);
      this.$refs.form.validate((valid) => {
        if (valid) {
          if (this.modeltype === 0) {
            axios({
              url: "http://yapi.smart-xwork.cn/mock/226692/postProject/work",
              method: "post",
              data: this.form,
            }).then(() => {
              this.getList();
            });
          } else {
            axios({
              url: "http://yapi.smart-xwork.cn/mock/226692/postProject/work",
              method: "post",
              data: this.form,
            }).then(() => {
              this.getList();
            });
          }
          // console.log(this.form, "form");
          this.form = {};
          this.$refs.form.resetFields();
          this.dialogVisible = false;
        }
      });
      // this.$refs.form.resetFields();
    },
    // 重置
    // handleclose() {
    //   this.$refs.form.resetFields();
    //   this.dialogVisible = false;
    //   this.edit = false;
    //   location.reload();
    // },
    // handleclose_1() {
    //   this.$refs.form.resetFields();
    //   this.dialogVisible_1 = false;
    //   this.edit = false;
    //   location.reload();
    // },
    // close() {
    //   this.handleclose();
    // },
    // close_1() {
    //   this.handleclose_1();
    // },
    getList() {
      axios({
        url: "http://yapi.smart-xwork.cn/mock/226692/project/work",
        method: "get",
      }).then((res) => {
        console.log(res.data);

        this.tabledata.unshift(res.data);

        return res.data;
      });
      // getUser().then(
      //   res => {
      //     console.log(res.data);
      //     console.log(JSON.parse(res.data));
      //     this.tabledata = res.data
      //     console.log(this.tabledata);
      //   }
      // );
    },
    handleDelete(row) {
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          delUser({ id: row.id }).then(() => {
            this.$message({
              type: "success",
              message: "删除成功!",
            });
            this.getList();
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    async handleEdit(row) {
      // this.edit = true;
      this.timer = new Date().getTime();
      this.thisRow = row;
      this.modeltype = 1;
      this.dialogVisible = true;
      //需要对当前行数据进行深拷贝，否则会出错
      this.form = JSON.parse(JSON.stringify(row));
    },
    projectInfo(row) {
      // this.modeltype = 1;
      this.timer = new Date().getTime(); //用于控制子组件viewProject，每次点击按钮时重新调用一次子组件
      this.dialogVisible_1 = true;
      console.log(row);
      //需要对当前行数据进行深拷贝，否则会出错
      this.thisRow = row;
      this.form = JSON.parse(JSON.stringify(row));

      // pipei = JSON.parse(JSON.stringify(row));
      // return pipei
    },
    onSubmit() {
      this.getList();
    },
    remoteMethod(query) {
      if (query !== "") {
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.options = this.list.filter((item) => {
            return item.label.toLowerCase().indexOf(query.toLowerCase()) > -1;
          });
        }, 200);
      } else {
        this.options = [];
      }
    },
  },

  async mounted() {
    axios({
      url: "http://yapi.smart-xwork.cn/mock/226692/project/work",
      method: "get",
    }).then((res) => {
      console.log(res.data);

      this.tabledata.unshift(res.data);
      return res.data;
    });
    // this.getList();
    this.list = this.states.map((item) => {
      return { value: `value:${item}`, label: `label:${item}` };
    });
    const { data: res } = await this.$http.get(
      "http://yapi.smart-xwork.cn/mock/226692/project/department"
    );
    this.department = res;
    const { data: res1 } = await this.$http.get(
      "http://yapi.smart-xwork.cn/mock/226692/project/room"
    );
    this.room = res1;

    // await this.$http.get(
    //   "http://yapi.smart-xwork.cn/mock/226692/project/work"
    // ).then(res =>{
    //   this.tabledata = res
    // })
  },
};
</script>
<style lang="less" scoped>
.manage {
  display: felx;
  height: auto;

  .common-tabel {
    height: 510px;
  }

  .manage-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
</style>