<template>
  <div>
    <el-upload
      class="upload-demo"
      ref="upload"
      action="https://jsonplaceholder.typicode.com/posts/"
      :on-preview="handlePreview"
      :on-remove="handleRemove"
      :file-list="fileList"
      :auto-upload="false"
    >
      <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
      <el-button
        style="margin-left: 10px"
        size="small"
        type="success"
        @click="submitUpload"
        >上传文件</el-button
      >
    </el-upload>

    <!-- 属性表单 -->
    <el-form :model="file" :inline="true" :rules="rules" ref="form">
      <el-form-item label="请选择所属科室" prop="room_name">
        <div>
          <pick v-if="room[0]" :Options="room" v-model="file.room_name"></pick>
        </div>
      </el-form-item>
      <el-form-item label="图文档名称" prop="file_name">
        <el-input v-model="file.file_name"></el-input>
      </el-form-item>
      <el-form-item label="图文档编号" prop="file_id">
        <el-input v-model="file.file_id"></el-input>
      </el-form-item>
      <el-form-item label="创建工时" prop="creator_work_hour">
        <el-input v-model="file.creator_work_hour"></el-input>
      </el-form-item>
      <el-form-item label="请选择校对人" prop="proof_person ">
        <div>
          <pick
            v-if="room[0]"
            :Options="room"
            v-model="file.proof_person"
          ></pick>
        </div>
      </el-form-item>
      <el-form-item label="请选择审核人" prop="examine_person">
        <div>
          <pick
            v-if="room[0]"
            :Options="room"
            v-model="file.examine_person"
          ></pick>
        </div>
      </el-form-item>
      <el-form-item label="请选择难度系数" prop="difficulty_degree">
        <div>
          <el-radio v-model="file.difficulty_degree" label="1">1</el-radio>
          <el-radio v-model="file.difficulty_degree" label="1.2">1.2</el-radio>
          <el-radio v-model="file.difficulty_degree" label="1.5">1.5</el-radio>
        </div>
      </el-form-item>
      <div>
        <el-form-item label="贡献度" prop="contribute_degree">
          <el-input v-model="file.contribute_degree"></el-input>
        </el-form-item>
      </div>
      <el-form-item label="请输入文档内容说明" prop="file_desc">
        <el-input
          type="textarea"
          :rows="5"
          placeholder="请输入内容"
          v-model="file.file_desc"
        >
        </el-input>
      </el-form-item>
      <div>
        <el-form-item label="请选择是否发布" prop="is_publish">
          <div>
            <el-radio v-model="file.is_publish" label="1">是</el-radio>
            <el-radio v-model="file.is_publish" label="0">否</el-radio>
          </div>
        </el-form-item>
      </div>
      <div>
        <el-form-item label="请选择是否供外" prop="is_give_out">
          <el-radio v-model="file.is_give_out" label="1">是</el-radio>
          <el-radio v-model="file.is_give_out" label="0">否</el-radio>
        </el-form-item>
      </div>
      <el-form-item label="供外生命周期" prop="give_out_life">
        <el-input v-model="file.give_out_life"></el-input>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import pick from "../pick.vue";
export default {
  components: { pick },
  data() {
    return {
      room: [],
      file: {}, //文件属性
    };
  },
  methods: {
    submitUpload() {
      this.$refs.upload.submit();
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    },
  },
  async created() {
    // 请求科室模板数据
    const { data: res2 } = await this.$http.get(
      "http://yapi.smart-xwork.cn/mock/226692/postProject/room"
    );
    this.room = res2;
    // --------------------------------
  },
};
</script>
