<template>
  <el-form :model="form" :inline="true" :rules="rules" ref="form">
    <el-divider content-position="left">合同信息</el-divider>
    <el-form-item label="项目名称" prop="project_name">
      <el-input v-model="form.project_name"></el-input>
    </el-form-item>
    <el-form-item label="项目令号" prop="project_id">
      <el-input v-model="form.project_id"></el-input>
    </el-form-item>

    <el-form-item label="合同交期" prop="contract_date">
      <el-col :span="11">
        <el-form-item>
          <el-date-picker
            type="date"
            placeholder="选择日期"
            v-model="form.contract_date"
            style="width: 200px"
          ></el-date-picker>
        </el-form-item>
      </el-col>
    </el-form-item>

    <el-form-item label="销售经理" prop="sale_manager">
      <el-input v-model="form.sale_manager"></el-input>
    </el-form-item>
    <el-form-item label="合同金额" prop="contract_money">
      <el-input v-model.number="form.contract_money"></el-input>
    </el-form-item>

    <el-divider content-position="left">设计信息</el-divider>
    <el-form-item label="电仪设计节点" prop="time_dy">
      <el-input v-model="form.time_dy"></el-input>
    </el-form-item>
    <el-form-item label="设备设计节点" prop="time_sb">
      <el-input v-model="form.time_sb"></el-input>
    </el-form-item>
    <el-form-item label="设计经理" prop="design_manager">
      <el-input v-model="form.design_manager"></el-input>
    </el-form-item>
    <el-form-item label="服务人员">
      <el-input v-model="form.serve_person"></el-input>
    </el-form-item>

    <el-divider content-position="left">物流信息</el-divider>
    <el-form-item label="发货方式" prop="deliver_way">
      <el-input v-model="form.deliver_way"></el-input>
    </el-form-item>
    <el-form-item label="订货单位" prop="order_unit">
      <el-input v-model="form.order_unit"></el-input>
    </el-form-item>
    <el-form-item label="收货单位" prop="receive_unit">
      <el-input v-model="form.receive_unit"></el-input>
    </el-form-item>
    <el-form-item label="收货地址" prop="receive_address">
      <el-input v-model="form.receive_address"></el-input>
    </el-form-item>
    <el-form-item label="到站" prop="arrive_station">
      <el-input v-model="form.arrive_station"></el-input>
    </el-form-item>
    <el-form-item label="邮编" prop="mail">
      <el-input v-model="form.mail"></el-input>
    </el-form-item>
    <el-form-item label="联系人" prop="contact_people">
      <el-input v-model="form.contact_people"></el-input>
    </el-form-item>
    <el-form-item label="联系方式" prop="contact_people_phone">
      <el-input v-model="form.contact_people_phone"></el-input>
    </el-form-item>
    <el-form-item label="传真" prop="fax">
      <el-input v-model="form.fax"></el-input>
    </el-form-item>
    <el-form-item label="发货单位" prop="send_unit">
      <el-input v-model="form.send_unit"></el-input>
    </el-form-item>
    <el-form-item label="发货条件" prop="send_condition">
      <el-input v-model="form.send_condition"></el-input>
    </el-form-item>
    <!-- 选择部门 -->
    <el-divider content-position="left">请选择部门</el-divider>
    <el-form-item>
      <pick v-if="department[0]" :Options="department"></pick>
    </el-form-item>
    <el-divider content-position="left">请选择科室</el-divider>
    <div>
      <pick v-if="room[0]" :Options="room"></pick>
    </div>
    <!-- 选择负责人 -->
    <div class="leader">
      <el-divider content-position="left">请选择审批人</el-divider>
      <div>
        <pick :Options="[1, 1]"></pick>
      </div>
    </div>
    <!-- 选择项目成员 -->
    <div class="member">
      <el-divider content-position="left">请选择项目成员</el-divider>
      <div>
        <pick :Options="[1, 1]"></pick>
      </div>
    </div>
    <!-- 选择资料上传类型列表 -->
    <div class="fileType">
      <el-divider content-position="left"
        >请选择技术资料类型上传列表</el-divider
      >
      <div>
        <pick :Options="[1, 1]"></pick>
      </div>
    </div>

    <el-form-item>
      <el-button type="primary" @click="submitForm('form')">立即创建</el-button>
      <el-button @click="resetForm('form')">重置</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
// import DropdownList from '../ui/dropdownList.vue'
import pick from "./pick.vue";
export default {
  components: { pick },
  // props: ["editOrNew"],
  props: ["thisRow"],
  data() {
    return {
      // newOrEdit: this.editOrNew,
      department: [],
      room: [],
      form: this.thisRow,
      // form: {
      //   project_name: "",
      //   project_id: "",
      //   project_members: [],
      //   project_approves: [],
      //   document_type: [],
      //   contract_date: "",
      //   sale_manager: "",
      //   contract_money: "",
      //   design_manager: "",
      //   serve_person: [],
      //   receive_address: "",
      //   deliver_way: "",
      //   order_unit: "",
      //   receive_unit: "",
      //   arrive_station: "",
      //   mail: "",
      //   contact_people: "",
      //   contact_people_phone: "",
      //   fax: "",
      //   send_unit: "",
      //   time_dy: "",
      //   time_sb: "",
      //   send_condition: "",
      // },
      // 表单验证规则
      rules: {
        project_name: [
          { required: true, message: "项目名称不能为空", trigger: "blur" },
        ],
        project_id: [
          { required: true, message: "项目令号不能为空", trigger: "blur" },
          {
            pattern: /^[^\u4e00-\u9fa5]+$/,
            message: "不允许输入中文",
            trigger: "change",
          },
          {
            pattern: /^[A-Za-z0-9\u4e00-\u9fa5]+$/,
            message: "不允许输入空格等特殊符号",
            trigger: "change",
          },
        ],
        time_dy: [
          { required: true, message: "电仪节点不能为空", trigger: "blur" },
        ],
        time_sb: [
          { required: true, message: "设备节点不能为空", trigger: "blur" },
        ],
        sale_manager: [
          {
            pattern: /^([\\u4e00-\\u9fa5]{1,20}|[a-zA-Z\\.\\s]{1,20})$/,
            message: "请输入正确的名字",
            trigger: "change",
          },
        ],
        contract_money: [
          { type: "number", message: "请输入数字", trigger: "change" },
        ],
        mail: [
          {
            pattern: /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/,
            min: 9,
            max: 18,
            message: "邮箱格式错误",
            trigger: "change",
          },
        ],
        contact_people_phone: [
          {
            pattern: /^1[3-9]\d{9}$/,
            min: 11,
            max: 11,
            message: "手机号格式错误",
            trigger: "blur",
          },
        ],
      },
      // --------------------------------------
    };
  },
  methods: {
    async getData() {
      // // 请求表格数据
      // const { data: res } = await this.$http.get(
      //   "http://yapi.smart-xwork.cn/mock/226692/project/work"
      // );
      // this.newOrEdit ? (this.form = res) : (this.form = {});
      // console.log(this.form);
      // --------------------------------
      // 请求部门模板数据
      const { data: res1 } = await this.$http.get(
        "http://yapi.smart-xwork.cn/mock/226692/project/department"
      );
      this.department = res1;
      // --------------------------------
      // 请求科室模板数据
      const { data: res2 } = await this.$http.get(
        "http://yapi.smart-xwork.cn/mock/226692/postProject/room"
      );
      this.room = res2;
      // --------------------------------
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
      this.$http({
        method: "post",
        url: "http://yapi.smart-xwork.cn/mock/226692/postProject/work",
        data: this.form,
        headers: {
          "Content-Type": "	application/x-www-form-urlencoded",
        },
      });
      // this.$http
      //   .get("http://yapi.smart-xwork.cn/mock/226692/postProject/work")
      //   .then((res) => {
      //     console.log(res.data);
      //   })
      //   .catch((err) => {
      //     console.log(err);
      //   });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
      this.form = {};
    },
  },
  created() {
    this.getData();
  },
};
</script>
<style lang="less" scoped>
label {
  margin-left: 30px;
  margin-top: 10px;
}

input {
  outline: none;
  height: 20px;
  width: 250px;
  margin-left: 2px;
  margin-top: 10px;
}
</style>