<template>
  <div>
    <el-dialog
      title=""
      :visible.sync="dialogVisible_1"
      width="70%"
      append-to-body
    >
      <Upload />
    </el-dialog>
    <el-dialog
      title=""
      :visible.sync="dialogVisible_2"
      width="70%"
      append-to-body
    >
      <View />
    </el-dialog>

    <el-table :data="a" style="width: 100%">
      <el-table-column prop="datatype" label="技术资料列表" width="500">
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button type="primary" size="mini" @click="upload()"
            >上传</el-button
          >
          <el-button type="primary" size="mini" @click="download()"
            >下载</el-button
          >
          <el-button type="primary" size="mini" @click="view()">预览</el-button>
          <el-button type="primary" size="mini" @click="edit()">编辑</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
import Upload from "../uploadDocument/Upload.vue";
import View from "../uploadDocument/View.vue";

export default {
  props: ["tabledata"],
  components: {
    Upload,
    View,
  },
  data() {
    return {
      a: [],
      b: {
        datatype: "",
      },
      dialogVisible_1: false,
      dialogVisible_2: false,

      tableData: this.tabledata.document_type,
    };
  },
  methods: {
    upload() {
      this.dialogVisible_1 = true;
    },
    view() {
      this.dialogVisible_2 = true;
    },
    edit() {
      this.dialogVisible_1 = true;
    },
  },
  created() {
    console.log(this.tableData);
    for (let i = 0; i < this.tableData.length; i++) {
      this.b = {
        datatype: this.tableData[i],
      };
      this.a.push(this.b);
      // console.log(this.a);
      // console.log(this.b);
    }
    // console.log(this.a);
  },
};
</script>
