<template>
    <el-row>
        <el-col :span="8">
            <el-card>
                <div class="user">
                    <!-- action是上传地址 -->
                    <el-upload class="upload" action="https://jsonplaceholder.typicode.com/posts/"
                        :show-file-list="false">
                        <img class="avatar" v-if="imageUrl" :src="imageUrl">
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                    <div class="user-info">
                        <p class="name">Admin</p>
                        <p class="access">13012340000</p>
                    </div>
                    <div style="width: 120px;height: 30px;margin-bottom: -170px;
                    margin-right: -34px;">
                        修改账号
                    </div>
                </div>
                <div class="account-info">
                    <p class="">职 务：</p>
                    <p class="">所属部门：</p>
                    <p class="">所属科室：</p>
                </div>
            </el-card>
        </el-col>

        <el-col :span="15" style="padding-left: 40px;">
            <el-card style="margin-top: 20px;height: auto;">
                <p>参与项目：</p>
                <el-table :data="tableData" style="width: 100%">
                    <el-table-column v-for="(val,key) in tableLabel" :prop="key" :label="val" />
                </el-table>
            </el-card>
        </el-col>
    </el-row>
</template>
<script>
    export default {
        data() {
            return {
                imageUrl: '',
                tableData: [
                    {
                        name: '项目一',
                        introduce: '项目简介.....',
                        member: '人员一、人员二',
                        url: '跳转'
                    },
                ],
                tableLabel: {
                    name: '项目名称',
                    introduce: '项目简介',
                    member: '参与人员',
                    url: '跳转'
                }
            };
        },
        methods: {
            handleAvatarSuccess(res, file) {
                this.imageUrl = URL.createObjectURL(file.raw);
            },
            beforeAvatarUpload(file) {
                const isJPG = file.type === 'image/jpeg';
                const isLt2M = file.size / 1024 / 1024 < 2;

                if (!isJPG) {
                    this.$message.error('上传头像图片只能是 JPG 格式!');
                }
                if (!isLt2M) {
                    this.$message.error('上传头像图片大小不能超过 2MB!');
                }
                return isJPG && isLt2M;
            }
        }
    }
</script>
<style>
    .el-upload {
        border: 1px dashed #adaaaa;
        border-radius: 50%;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    }

    .el-upload hover {
        border-color: #409EFF;
    }

    .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 178px;
        height: 178px;
        line-height: 178px;
        text-align: center;
    }

    .avatar {
        width: 178px;
        height: 178px;
        display: block;
    }

    .user {
        padding-bottom: 20px;
        margin-bottom: 20px;
        border-bottom: 1px solid #ccc;
        display: flex;
        align-items: center;
    }

    .user-info {
        margin-left: 40px;

        .name {
            font-size: 32px;
            margin-bottom: 10px
        }

        .access {
            color: #999999;
        }
    }

    .account-info {
        font-size: 20px;
        margin-left: 20px;
    }
</style>